/*add css by rohit kumar*/
.card_listing {  }
.card_listing .seller_list_card { box-shadow: 0px 0px 12px -9px #000; -moz-box-shadow: 0px 0px 12px -9px #000; -webkit-box-shadow: 0px 0px 12px -9px #000; -ms-box-shadow: 0px 0px 12px -9px #000; transition: 0.5s ease-in-out; -moz-transition: 0.5s ease-in-out; -webkit-transition: 0.5s ease-in-out; -ms-transition: 0.5s ease-in-out; }
.card_listing .seller_list_card:hover { background:#e9edf0; transition: 0.5s ease-in-out; -moz-transition: 0.5s ease-in-out; -webkit-transition: 0.5s ease-in-out; -ms-transition: 0.5s ease-in-out;  }
.card_listing .seller_list_card .card-body { padding: 15px 15px; position: relative; }
.card_listing .seller_list_card .card-body .card-img-top { height: 220px !important; }
.card_listing .seller_list_card .card-title { text-align:center; width: 100%; display: inline-block; padding: 0px 25px;  margin: 10px 0px 0px; border-bottom: 0px; color:#000; font-weight: 700; text-transform: uppercase; }
.card_listing .seller_list_card a { position: absolute; top: 200px; left: 25px; opacity:0; visibility: hidden; transition: 0.5s ease-in-out; -moz-transition: 0.5s ease-in-out; -webkit-transition: 0.5s ease-in-out; -ms-transition: 0.5s ease-in-out; }
.card_listing .seller_list_card a.card-link { left: auto; right: 25px; }
.card_listing .seller_list_card a button { border-radius: 5px !important; transition: 0.5s ease-in-out; -moz-transition: 0.5s ease-in-out; -webkit-transition: 0.5s ease-in-out; -ms-transition: 0.5s ease-in-out; }
.card_listing .seller_list_card a button.btn-edit { background:#fff; }
.card_listing .seller_list_card a button i { margin: 0px; }
.card_listing .seller_list_card a button:hover { background:#000 !important; opacity:1; color:#fff; transition: 0.5s ease-in-out; -moz-transition: 0.5s ease-in-out; -webkit-transition: 0.5s ease-in-out; -ms-transition: 0.5s ease-in-out; }
.card_listing .seller_list_card .card-body:hover a { opacity:1; visibility: visible; transition: 0.5s ease-in-out; -moz-transition: 0.5s ease-in-out; -webkit-transition: 0.5s ease-in-out; -ms-transition: 0.5s ease-in-out;  }
.card_listing .seller_list_card .card-body:hover:hover .card-title {  }

